@import url(https://fonts.googleapis.com/css?family=Roboto:regular,bold,medium&subset=latin,latin-ext);
@import url(https://fonts.googleapis.com/css?family=Noto+Sans+JP:regular,bold,medium&subset=latin,latin-ext);
.ProductFormDescInput{
    font-size: 13px;
    color: #333333;
    border: 0px solid white;
    width:100%;
    background-color:white;
    font-family: Noto Sans JP;
}
.ProductFormDescInput:focus{
    border: 0px solid white;
}
.react-tagsinput {
    background-color: #fff;
    overflow: hidden;
    padding-left: 5px;
    padding-top: 5px;
  }
  
  .react-tagsinput--focused {
    border-color: #ccc;
  }
  
  .react-tagsinput-tag {
    background-color: #fff;
    border-radius: 2px;
    border: 2px solid #BBA884;
    color: #BBA884;
    display: inline-block;
    font-family: Noto Sans JP;
    font-size: 13px;
    font-weight: 400;
    margin-bottom: 5px;
    margin-right: 5px;
    padding: 10px 15px;
    border-radius: 17px;
  }
  
  .react-tagsinput-remove {
    cursor: pointer;
    font-weight: bold;
  }
  
  .react-tagsinput-tag a::before {
    content: " ×";
  }
  
  .react-tagsinput-input {
    background: transparent;
    border: 0;
    color: #777;
    font-family: sans-serif;
    font-size: 13px;
    font-weight: 400;
    margin-bottom: 6px;
    margin-top: 1px;
    outline: none;
    padding: 5px;
    width: 80px;
  }
  
html,body {
    height: 100%;
    margin: 0px;
    padding: 0px;
}
#root{
    height: 100%;
}
#root > div{
    height: 100%;
}
::-webkit-scrollbar {
    display: none;
}




